.calculatorContainer {
    background: linear-gradient(145deg, rgba(30, 36, 50, 0.8), rgba(42, 52, 65, 0.9));
    padding: 1.5rem;
    border-radius: 12px;
    color: #ffffff;
    width: 90%;
    margin: 1rem auto;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}

/* ... Keep existing animations and particle styles ... */

.calcTitle {
    text-align: center;
    background: linear-gradient(145deg, rgba(30, 36, 50, 0.9), rgba(42, 52, 65, 0.4));
    color: #edeeef;
    font-size: clamp(1.2rem, 4vw, 1.8rem);
    margin-bottom: 1.5rem;
    padding: 0.8rem;
    border-radius: 8px;
}

.sliderContainer {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 1.5rem;
}

.sliderContainer label {
    background: rgba(13, 21, 28, 0.5);
    padding: 0.8rem;
    font-size: clamp(0.9rem, 3vw, 1.2rem);
    color: #fdfdfd;
    border-radius: 6px;
    margin-bottom: 0.3rem;
}

.sliderGroup {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.slider {
    width: 100%;
    height: 6px;
    margin: 0.5rem 0;
}

.sliderValue {
    min-width: 80px;
    padding: 0.5rem;
    background: rgba(79, 172, 254, 0.1);
    border: 1px solid #4facfe;
    border-radius: 4px;
    text-align: center;
    font-size: clamp(0.8rem, 3vw, 1.1rem);
}

.resultsContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    gap: 0.8rem;
    margin-top: 1.5rem;
}

.resultCard {
    background: rgba(79, 172, 254, 0.1);
    padding: 1rem;
    border-radius: 8px;
    text-align: center;
    border: 1px solid rgba(79, 172, 254, 0.2);
}

.resultCard h3 {
    color: #b1c4de;
    font-size: clamp(0.8rem, 3vw, 1.1rem);
    margin-bottom: 0.5rem;
}

.amount {
    font-size: clamp(0.9rem, 3vw, 1.2rem);
    color: #8bb9e5;
    font-weight: bold;
}

@media (min-width: 768px) {
    .calculatorContainer {
        width: 80%;
        max-width: 900px;
        padding: 2rem;
    }

    .sliderGroup {
        flex-direction: row;
        align-items: center;
    }

    .resultsContainer {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }
}

@media (max-width: 480px) {
    .calculatorContainer {
        width: 95%;
        padding: 1rem;
        margin: 0.5rem auto;
    }

    .sliderContainer label {
        padding: 0.6rem;
    }

    .resultCard {
        padding: 0.8rem;
    }
}