.historyContainer {
    margin-top: 4rem;
    padding: 2rem;
    background: linear-gradient(145deg, #1e2432, #2a3441);
    border-radius: 15px;
    max-width: 1200px;
    margin: 4rem auto;
}

.historyTitle {
    color: #00f2fe;
    font-size: 2rem;
    margin-bottom: 2rem;
    text-align: center;
}

.tableWrapper {
    overflow-x: auto;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.historyTable {
    width: 100%;
    border-collapse: collapse;
    background: rgba(26, 32, 44, 0.8);
}

.historyTable th,
.historyTable td {
    padding: 1rem;
    text-align: left;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.historyTable th {
    background: linear-gradient(90deg, #222b3d, #2d3748);
    color: #00f2fe;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 0.9rem;
}

.historyTable tr:hover {
    background: rgba(79, 172, 254, 0.1);
}

.status {
    padding: 0.4rem 0.8rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: bold;
}

.active {
    background: rgba(72, 187, 120, 0.2);
    color: #48bb78;
}

.completed {
    background: rgba(79, 172, 254, 0.2);
    color: #4facfe;
}

.expired {
    background: rgba(245, 101, 101, 0.2);
    color: #f56565;
}

@media (max-width: 768px) {
    .historyContainer {
        padding: 1rem;
        margin: 2rem 1rem;
    }

    .historyTable th,
    .historyTable td {
        padding: 0.8rem;
        font-size: 0.9rem;
    }
}