.minerContainer {
    padding: 2rem;
    border-radius: 15px;
    margin: 2rem 0;
}

.babyTheme {
    background: linear-gradient(135deg, 
        rgba(76, 171, 255, 0.2) 1%, 
        rgba(0, 242, 254, 0.5) 100%
    );
    box-shadow: 0 0 20px rgba(79, 172, 254, 0.2);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}
.babyTheme:hover {
    background: linear-gradient(135deg, 
        rgba(76, 171, 255, 0.3) 1%, 
        rgba(0, 242, 254, 0.6) 100%
    );
}
.eliteTheme {
    background: linear-gradient(135deg, 
        rgba(127, 0, 255, 0.2) 0%, 
        rgba(225, 0, 255, 0.5) 100%
    );
    box-shadow: 0 0 20px rgba(127, 0, 255, 0.2);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.eliteTheme:hover {
    background: linear-gradient(135deg, 
        rgba(127, 0, 255, 0.3) 0%, 
        rgba(225, 0, 255, 0.6) 100%
    );
}

.ultraTheme {
    background: linear-gradient(135deg, 
        rgba(255, 8, 68, 0.2) 0%, 
        rgba(255, 177, 153, 0.5) 100%
    );
    box-shadow: 0 0 20px rgba(255, 8, 68, 0.2);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.ultraTheme:hover {
    background: linear-gradient(135deg, 
        rgba(255, 8, 68, 0.3) 0%, 
        rgba(255, 177, 153, 0.6) 100%
    );
}

/* ... rest of shared styles ... */
.machineContainer {
    background: linear-gradient(135deg, #1a1f2c 0%, #2d3748 100%);
    min-height: 100vh;
    padding: 2rem;
    color: #ffffff;
}

.header {
    text-align: center;
    margin-bottom: 3rem;
}

.title {
    font-size: 2.5rem;
    background: linear-gradient(90deg, #00f2fe 0%, #4facfe 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 1rem;
}

.subtitle {
    color: #a0aec0;
    font-size: 1.2rem;
}

.machinesGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    padding: 1rem;
}

.machineCard {
    background: linear-gradient(145deg, #222b3d, #2d3748);
    border-radius: 15px;
    padding: 1.5rem;
    transition: all 0.3s ease;
    border: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;
    overflow: hidden;
}

.machineCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    border-color: #4facfe;
}

.machineType {
    font-size: 1.5rem;
    color: #4facfe;
    margin-bottom: 1rem;
}

.specs {
    margin: 1.5rem 0;
}

.specItem {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.8rem;
    padding-bottom: 0.8rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.specLabel {
    color: #a0aec0;
}

.specValue {
    color: #4facfe;
    font-weight: bold;
}

.price {
    font-size: 2rem;
    color: #00f2fe;
    margin: 1.5rem 0;
    text-align: center;
}

.rentButton {
    width: 100%;
    padding: 1rem;
    background: linear-gradient(90deg, #00f2fe 0%, #4facfe 100%);
    border: none;
    border-radius: 8px;
    color: #1a1f2c;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
}

.rentButton:hover {
    transform: scale(1.02);
    box-shadow: 0 0 20px rgba(79, 172, 254, 0.4);
}

.rentButton:disabled {
    background: #4a5568;
    cursor: not-allowed;
}

.durationSelector {
    margin: 1rem 0;
    padding: 0.8rem;
    background: rgba(234, 231, 231, 0.779);
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: rgb(0, 0, 0);
    width: 100%;
}


.statusBadge {
    position: absolute;
    top: 1rem;
    right: 1rem;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: bold;
}

.available {
    background: rgba(72, 187, 120, 0.2);
    color: #48bb78;
}

.rented {
    background: rgba(245, 101, 101, 0.2);
    color: #f56565;
}

.machinesGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    padding: 1rem;
    max-width: 1200px;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .machinesGrid {
        grid-template-columns: 1fr;
    }
    
    .machineContainer {
        padding: 1rem;
    }

    .title {
        font-size: 2rem;
    }

    .machineCard {
        padding: 1rem;
    }

    .price {
        font-size: 1.5rem;
    }
}

@keyframes glow {
    0% { box-shadow: 0 0 5px rgba(79, 172, 254, 0.2); }
    50% { box-shadow: 0 0 20px rgba(79, 172, 254, 0.4); }
    100% { box-shadow: 0 0 5px rgba(79, 172, 254, 0.2); }
}