.portfolioContainer {
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
    padding: 3rem;
    border-radius: 15px;
    color: #ffffff;
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
    width: 70%;
    margin-top: 3%;
    margin-left: 12%;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    padding-bottom: 1rem;
    border-bottom: 2px solid #ffd700;
}

.header h1 {
    font-size: 2.5rem;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.totalValue {
    font-size: 1.5rem;
    padding: 0.5rem 1rem;
    background: rgba(255, 215, 0, 0.1);
    border: 1px solid #ffd700;
    border-radius: 8px;
}

.assetsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
}

.assetCard {
    background: linear-gradient(145deg, #2a2a2a 0%, #333333 100%);
    border: 1px solid #ffd700;
    border-radius: 10px;
    padding: 1.5rem;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.assetCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 0 25px rgba(255, 215, 0, 0.3);
}

.assetHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.assetHeader h3 {
    font-size: 1.65rem;
    color: #ffd700;
}

.profit {
    font-weight: bold;
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    font-size: 1.3rem;
}

.positive {
    background: rgba(0, 255, 0, 0.1);
    color: #00ff00;
}

.negative {
    background: rgba(255, 0, 0, 0.1);
    color: #ff0000;
    font-size: 1.3rem;
}

.assetDetails {
    font-size: 1.4rem;
    line-height: 1.5;
}

.assetDetails p {
    margin: 0.5rem 0;
    color: #cccccc;
}