.investmentContainer {
    padding: 2rem;
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
    border-radius: 15px;
    margin: 2rem 0;
}

.title {
    text-align: center;
    color: #ffd700;
    font-size: 2.5rem;
    margin-bottom: 2rem;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.cardContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    justify-items: center;
}
.cardCall {
    background: linear-gradient(145deg, #1a472a, #2d694d);
    border-color: #00ff00;
    border-radius: 12px;
    padding: 1.5rem;
    width: 100%;
    max-width: 95%;
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
    transition: transform 0.3s ease;
}

.cardPut {
    background: linear-gradient(145deg, #4a1a1a, #692d2d);
    border-color: #ff0000;
    border-radius: 12px;
    padding: 1.5rem;
    width: 100%;
    max-width: 95%;
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
    transition: transform 0.3s ease;
}
.cardFuture {
    background: linear-gradient(145deg, #1a3c4a, #2d5669);
    border-color: #00bfff;
    border-radius: 12px;
    padding: 1.5rem;
    width: 100%;
    max-width: 95%;
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
    transition: transform 0.3s ease;
}

.cardOptions {
    background: linear-gradient(145deg, #4a4a1a, #69692d);
    border-color: #ffd700;
    border-radius: 12px;
    padding: 1.5rem;
    width: 100%;
    max-width: 95%;
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
    transition: transform 0.3s ease;
}
.card:hover {
    transform: translateY(-5px);
}

.cardHeader {
    text-align: center;
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ffd700;
}

.cardHeader h3 {
    /* color: #ffd700; */
    color:#ffffff;
    font-size: 1.8rem;
    margin-bottom: 0.5rem;
}

.cardHeader p {
    color: #bbffb16b;
    font-size: 2rem;
}

.buttonGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
}

.investBtn {
    background: linear-gradient(145deg, #ffd700, #ccad00);
    border: none;
    border-radius: 8px;
    padding: 0.8rem;
    color: #000;
    font-weight: bold;
    font-size: 1.1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.investBtn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

@media (max-width: 768px) {
    .cardContainer {
        grid-template-columns: 1fr;
    }

    .card {
        max-width: 100%;
    }

    .investBtn {
        font-size: 1rem;
        padding: 0.6rem;
    }
}





.rangeSelector {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin: 1rem 0;
    padding: 1rem;
    background: rgba(255, 215, 0, 0.05);
    border-radius: 8px;
    font-size: 2rem;
}

.rangeBtn {
    background: linear-gradient(145deg, #2a2a2a, #3d3d3d);
    border: 1px solid #666;
    border-radius: 6px;
    padding: 0.5rem;
    color: #dcd5a1;
    cursor: pointer;
    font-size: 1.5rem;
    transition: all 0.3s ease;
}

.rangeBtn:hover {
    background: linear-gradient(145deg, #3d3d3d, #2a2a2a);
    border-color: #ffd700;
    color: #ffd700;
}

.rangeBtn.selected {
    background: linear-gradient(145deg, #ffd700, #ccad00);
    border-color: #ffd700;
    color: #000;
    font-weight: bold;
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.3);
}



