.loaderContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    background: rgba(255, 215, 0, 0.05);
    border-radius: 8px;
    margin: 1rem 0;
}

.loader {
    width: 50px;
    height: 50px;
    border: 4px solid #2a2a2a;
    border-top: 4px solid #ffd700;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.2);
}

.loaderText {
    margin-top: 1rem;
    color: #ffd700;
    font-size: 1.8rem;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
}

.targetValue {
    margin-top: 0.5rem;
    color: #fff;
    font-size: 1.5rem;
    padding: 0.5rem 1rem;
    background: rgba(255, 215, 0, 0.1);
    border-radius: 4px;
    border: 1px solid #ffd700;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}


.categorySelector {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 2rem;
    padding: 1rem;
    background: rgba(255, 215, 0, 0.05);
    border-radius: 12px;
}

.categoryCard {
    width: 170px;
    padding: 1rem;
    border-radius: 8px;
    text-align: center;
    cursor: pointer;
    transition: all 0.3s ease;
    border: 2px solid transparent;
}

.categoryCard h3 {
    margin: 0;
    font-size: 1.8rem;
    color: #ffffff;
}
.categoryCard h4 {
    margin: 0;
    font-size: 1.2rem;
    color: #ffffff;
}

.categoryCard:hover {
    transform: translateY(-3px);
}

/* Category-specific styles */
.categoryCard:nth-child(1) {
    background: linear-gradient(145deg, #1a472a, #2d694d);
    border-color: #00ff00;
}

.categoryCard:nth-child(2) {
    background: linear-gradient(145deg, #4a1a1a, #692d2d);
    border-color: #ff0000;
}

.categoryCard:nth-child(3) {
    background: linear-gradient(145deg, #1a3c4a, #2d5669);
    border-color: #00bfff;
}

.categoryCard:nth-child(4) {
    background: linear-gradient(145deg, #4a4a1a, #69692d);
    border-color: #ffd700;
}

/* Active states */
.activeCall {
    box-shadow: 0 0 20px rgba(0, 255, 0, 0.3);
    transform: translateY(-3px);
}

.activePut {
    box-shadow: 0 0 20px rgba(255, 0, 0, 0.3);
    transform: translateY(-3px);
}

.activeFuture {
    box-shadow: 0 0 20px rgba(0, 191, 255, 0.3);
    transform: translateY(-3px);
}

.activeOptions {
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
    transform: translateY(-3px);
}

@media (max-width: 768px) {
    .categorySelector {
        flex-wrap: wrap;
        gap: 1rem;
    }

    .categoryCard {
        width: calc(50% - 0.5rem);
    }
}

.lockContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    background: rgba(255, 0, 0, 0.1);
    border-radius: 8px;
    margin: 1rem;
    border: 1px solid rgba(255, 0, 0, 0.3);
}

.lockIcon {
    font-size: 3rem;
    color: #ffd700;
    margin-bottom: 1rem;
    animation: pulse 2s infinite;
    text-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

.lockMessage {
    color: #fc2626;
    font-size: 1.8rem;
    text-align: center;
    font-weight: bold;
    text-shadow: 0 0 10px rgba(255, 0, 0, 0.3);
}

@keyframes pulse {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 0.7;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.cardFuture.locked {
    position: relative;
    opacity: 0.9;
}


.playAgainBtn {
    background: linear-gradient(145deg, #ffd700, #ccad00);
    border: none;
    padding: 0.8rem 1.5rem;
    border-radius: 8px;
    font-size: 1.1rem;
    color: #000;
    font-weight: bold;
    cursor: pointer;
    margin-top: 1rem;
    transition: all 0.3s ease;
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.2);
}

.playAgainBtn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.4);
}

.playAgainBtn:active {
    transform: scale(0.98);
}