/* module.css */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease-out;
}

.popup {
    background: linear-gradient(145deg, #2a2a2a, #3d3d3d);
    padding: 2rem;
    border-radius: 15px;
    border: 2px solid #ffd700;
    text-align: center;
    position: relative;
    width: 90%;
    max-width: 400px;
    animation: dropIn 0.5s ease-out;
    box-shadow: 0 0 30px rgba(255, 215, 0, 0.3);
}

.popup h2 {
    color: #ffd700;
    font-size: 2rem;
    margin-bottom: 1rem;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.content {
    margin: 2rem 0;
}

.message {
    color: #ffffff;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.coins {
    font-size: 2rem;
    margin: 1rem 0;
    animation: bounce 1s infinite;
}

.closeButton {
    background: linear-gradient(145deg, #ffd700, #ccad00);
    border: none;
}
