.premiumContainer {
    background: linear-gradient(135deg, 
        rgba(255, 252, 71, 0.2) 0%, 
        rgba(134, 138, 2, 0.5) 100%
    );
    min-height: 100vh;
    padding: 2rem;
    color: #ffffff;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.premiumContainer:hover {
    background: linear-gradient(135deg, 
        rgba(255, 252, 71, 0.3) 0%, 
        rgba(134, 138, 2, 0.6) 100%
    );
}

.premiumLockContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: linear-gradient(135deg, #1a1f2c 0%, #2d3748 100%);
}

.lockContent {
    text-align: center;
    padding: 3rem;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    backdrop-filter: blur(10px);
}

.lockIcon {
    font-size: 4rem;
    color: #ffd700;
    margin-bottom: 1rem;
    animation: float 3s ease-in-out infinite;
}

.upgradeButton {
    margin-top: 2rem;
    padding: 1rem 2rem;
    background: linear-gradient(90deg, #ffd700, #ffed4a);
    border: none;
    border-radius: 8px;
    color: #1a1f2c;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
}

.premiumBadge {
    position: absolute;
    top: -10px;
    right: -10px;
    background: linear-gradient(90deg, #ffd700, #ffed4a);
    color: #1a1f2c;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-weight: bold;
    font-size: 0.9rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.features {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin: 1rem 0;
}

.featureTag {
    background: rgba(255, 215, 0, 0.1);
    border: 1px solid #ffd700;
    color: #ffd700;
    padding: 0.3rem 0.8rem;
    border-radius: 15px;
    font-size: 0.9rem;
}

@keyframes float {
    0% { transform: translateY(0px); }
    50% { transform: translateY(-20px); }
    100% { transform: translateY(0px); }
}

.machineContainer {
    background: linear-gradient(135deg, #1a1f2c 0%, #2d3748 100%);
    min-height: 100vh;
    padding: 2rem;
    color: #ffffff;
}

.header {
    text-align: center;
    margin-bottom: 3rem;
}

.title {
    font-size: 3.2rem;
    background: linear-gradient(90deg, #010404 4%, #2d4153 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 1rem;
}

.subtitle {
    color: #e2ebf7;
    font-size: 1.5rem;
}

.machinesGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    padding: 1rem;
}

.machineCard {
    background: linear-gradient(145deg, #222b3d, #2d3748);
    border-radius: 15px;
    padding: 1.5rem;
    transition: all 0.3s ease;
    border: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;
    overflow: hidden;
}

.machineCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    border-color: #4facfe;
}

.machineType {
    font-size: 1.5rem;
    color: #4facfe;
    margin-bottom: 1rem;
}

.specs {
    margin: 1.5rem 0;
}

.specItem {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.8rem;
    padding-bottom: 0.8rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.specLabel {
    color: #a0aec0;
}

.specValue {
    color: #4facfe;
    font-weight: bold;
}

.price {
    font-size: 2rem;
    color: #00f2fe;
    margin: 1.5rem 0;
    text-align: center;
}

.rentButton {
    width: 100%;
    padding: 1rem;
    background: linear-gradient(90deg, #00f2fe 0%, #4facfe 100%);
    border: none;
    border-radius: 8px;
    color: #1a1f2c;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
}

.rentButton:hover {
    transform: scale(1.02);
    box-shadow: 0 0 20px rgba(79, 172, 254, 0.4);
}

.rentButton:disabled {
    background: #4a5568;
    cursor: not-allowed;
}

.durationSelector {
    margin: 1rem 0;
    padding: 0.8rem;
    background: rgba(234, 231, 231, 0.779);
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: rgb(0, 0, 0);
    width: 100%;
}


.statusBadge {
    position: absolute;
    top: 1rem;
    right: 1rem;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: bold;
}

.available {
    background: rgba(72, 187, 120, 0.2);
    color: #48bb78;
}

.rented {
    background: rgba(245, 101, 101, 0.2);
    color: #f56565;
}

.machinesGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    padding: 1rem;
    max-width: 1200px;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .machinesGrid {
        grid-template-columns: 1fr;
    }
    
    .machineContainer {
        padding: 1rem;
    }

    .title {
        font-size: 2rem;
    }

    .machineCard {
        padding: 1rem;
    }

    .price {
        font-size: 1.5rem;
    }
}

@keyframes glow {
    0% { box-shadow: 0 0 5px rgba(79, 172, 254, 0.2); }
    50% { box-shadow: 0 0 20px rgba(79, 172, 254, 0.4); }
    100% { box-shadow: 0 0 5px rgba(79, 172, 254, 0.2); }
}