.teamContainer {
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
    padding: 1rem;
    min-height: 100vh;
    color: #ffffff;
    width: 80%;
    overflow-x: auto;
    margin-left: 5%;
}

.header {
    text-align: center;
    margin-bottom: 3rem;
    border-bottom: 2px solid #ffd700;
    padding-bottom: 1rem;
}

.header h1 {
    font-size: 2.7rem;
    color: #ffd700;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
    margin-bottom: 0.5rem;
}

.header p {
    color: #cccccc;
    font-size: 1.8rem;
}

.treeContainer {
    display: flex;
    justify-content: flex-start;
    padding: 1rem;
    min-width: min-content;
    overflow-x: auto;
    margin-left: 5%;
}

.nodeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.teamCard {
    background: linear-gradient(145deg, #2a2a2a 0%, #333333 100%);
    border: 2px solid #ffd700;
    border-radius: 10px;
    padding: 1rem;
    margin: 0.5rem;
    width: 220px;
    min-width: 200px;
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

@media screen and (max-width: 768px) {
    .treeContainer{
        margin-left: -20%;
    }
    .teamContainer {
        padding: 0.5rem;
      
    }

    .header h1 {
        font-size: 2rem;
    }

    .header p {
        font-size: 1rem;
    }

    .teamCard {
        width: 180px;
        min-width: 160px;
        padding: 0.8rem;
    }

    .cardContent h3 {
        font-size: 1rem;
    }
    .role, .level, .earnings {
        font-size: 0.8rem;
        padding: 0.2rem 0.4rem;
    }

    .childNodes {
        gap: 1rem;
        padding-top: 1.5rem;
    }
}

@media screen and (max-width: 480px) {
    .treeContainer {
        padding: 0.5rem;
        margin-left: -20%;
        
    }

    .teamCard {
        width: 150px;
        min-width: 140px;
        margin: 0.3rem;
    }

    .childNodes {
        gap: 0.5rem;
    }

    .cardContent h3 {
        font-size: 0.9rem;
        margin-bottom: 0.5rem;
    }

    .cardDetails {
        gap: 0.3rem;
        /* font-size: 2rem; */
    }
}

.treeContainer::-webkit-scrollbar {
    height: 8px;
}

.treeContainer::-webkit-scrollbar-track {
    background: #1a1a1a;
    border-radius: 4px;
}

.treeContainer::-webkit-scrollbar-thumb {
    background: #ffd700;
    border-radius: 4px;
}

.treeContainer::-webkit-scrollbar-thumb:hover {
    background: #ffed4a;
}

.teamCard:hover {
    transform: translateY(-5px);
    box-shadow: 0 0 25px rgba(255, 215, 0, 0.4);
}

.cardContent h3 {
    color: #ffd700;
    margin-bottom: 1rem;
    font-size: 1.6rem;
    text-align: center;
}

.cardDetails {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    /* font-size: 2.5rem; */
}

.role, .level, .earnings {
    padding: 0.3rem 0.6rem;
    border-radius: 4px;
    font-size: 1.4rem;
    text-align: center;
}

.role {
    background: rgba(255, 215, 0, 0.1);
    color: #ffd700;
}

.level {
    background: rgba(0, 191, 255, 0.1);
    color: #00bfff;
}

.earnings {
    background: rgba(50, 205, 50, 0.1);
    color: #32cd32;
}

.childNodes {
    display: flex;
    gap: 2rem;
    padding-top: 2rem;
    position: relative;
}

.childNodes::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 2px;
    height: 2rem;
    background: #ffd700;
}

.childNodes > .nodeContainer::before {
    content: '';
    position: absolute;
    top: -2rem;
    left: 50%;
    width: 50%;
    height: 2px;
    background: #ffd700;
}

.childNodes > .nodeContainer:first-child::before {
    left: 50%;
}

.childNodes > .nodeContainer:last-child::before {
    right: 50%;
}