.resultContainer {
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
    padding: 2rem;
    border-radius: 15px;
    color: #ffffff;
    min-height: 40vh;
}

.title {
    text-align: center;
    color: #ffd700;
    font-size: 2.5rem;
    margin-bottom: 2rem;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.numberContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    justify-content: center;
    padding: 1rem;
    max-height: 70vh;
    overflow-y: auto;
}

.numberCard {
    width: 80px;
    height: 120px;
    background: linear-gradient(145deg, #2a2a2a, #3d3d3d);
    border: 2px solid #ffd700;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    cursor: pointer;
}

.numberCard.active {
    transform: scale(1.1);
    box-shadow: 0 0 30px rgba(255, 215, 0, 0.5);
    animation: pulse 0.5s infinite;
}

.numberCard.high {
    border-color: #00ff00;
    background: linear-gradient(145deg, #1a472a, #2d694d);
   
}

.numberCard.low {
    border-color: #ff0000;
    background: linear-gradient(145deg, #4a1a1a, #692d2d);
    
}

.number {
    font-size: 1.5rem;
    font-weight: bold;
    color: #ffffff;
}

.finalResult {
    margin-top: 2rem;
    text-align: center;
}

.resultCard {
    display: inline-block;
    padding: 2rem;
    background: linear-gradient(145deg, #ffd700, #ccad00);
    border-radius: 12px;
    animation: appearFromBottom 0.5s ease-out;
}

.resultCard h2 {
    color: #000;
    margin-bottom: 1rem;
}

.finalNumber {
    font-size: 2.5rem;
    font-weight: bold;
    color: #000;
}

@keyframes pulse {
    0% { transform: scale(1.1);}
    50% { transform: scale(1.19); }
    100% { transform: scale(1.1); }
}

@keyframes appearFromBottom {
    from {
        transform: translateY(50px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
        
    }
}

/* Custom scrollbar */
.numberContainer::-webkit-scrollbar {
    width: 8px;
}

.numberContainer::-webkit-scrollbar-track {
    background: #1a1a1a;
}

.numberContainer::-webkit-scrollbar-thumb {
    background: #ffd700;
    border-radius: 4px;
}

.numberContainer::-webkit-scrollbar-thumb:hover {
    background: #ccad00;
}

@media (max-width: 768px) {
    .numberCard {
        width: 60px;
        height: 90px;
    }

    .number {
        font-size: 1rem;
    }
}

.finalResultContainer {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-top: 2rem;
    padding: 1rem;
    
}

.resultCard {
    padding: 2rem;
    background: linear-gradient(145deg, #2a2a2a, #3d3d3d);
    border-radius: 12px;
    min-width: 200px;
    text-align: center;
    border: 2px solid #ffd700;
    animation: appearFromBottom 0.5s ease-out;
}

.callValue {
    color: #00ff00;
    text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
    background: linear-gradient(145deg, #1a472a, #2d694d);
    border-color: #00ff00;
}

.putValue {
    color: #ff0000;
    text-shadow: 0 0 10px rgba(255, 0, 0, 0.5);
    background: linear-gradient(145deg, #4a1a1a, #692d2d);
    border-color: #ff0000;
}

@media (max-width: 768px) {
    .finalResultContainer {
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }

    .resultCard {
        width: 80%;
        min-width: unset;
    }
}

.callValue h2{
    color:white;
  
}
.putValue h2{
    color:white;
}

