.timeContainer {
    top: 0.1rem;
    right: 0rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    background: linear-gradient(145deg, #b93232, #3d3d3d);
    padding: 0.8rem 1.2rem;
    border-radius: 8px;
    border: 1px solid #ffd700;
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.2);
}

.time {
    font-size: 2.9rem;
    font-weight: bold;
    color: #ffd700;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
    font-family: 'Digital', monospace;
}

.date {
    font-size: 1.9rem;
    color: #cccccc;
    margin-top: 0.2rem;
}

@media (max-width: 768px) {
    .timeContainer {
        position: relative;
        top: 0;
        right: 0;
        margin: 1rem auto;
        align-items: center;
    }

    .time {
        font-size: 3rem;
    }
}

.nextSpinContainer {
    background: linear-gradient(145deg, #2a2a2a, #3d3d3d);
    padding: 1rem;
    border-radius: 8px;
    margin: 1rem auto;
    text-align: center;
    border: 1px solid #ffd700;
    max-width: 450px;
}

.nextSpinText {
    color: #ffd700;
    font-size: 1.8rem;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
}