.tradingContainer {
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
    min-height: 100vh;
    color: #ffffff;
    padding: 2rem;
    width: 99%;
}

.header {
    text-align: center;
    margin-bottom: 3rem;
}

.header h1 {
    font-size: 3rem;
    color: #ffd700;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
    margin-bottom: 0.5rem;
}
.header h2{
    font-size: 2rem;
    color: #ffd700;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
    margin-bottom: 0.5rem;

}

.subtitle {
    color: #cccccc;
    font-size: 1.6rem;
}

.marketStats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-bottom: 3rem;
}

.statCard {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    padding: 1.5rem;
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.statCard h3 {
    color: #cccccc;
    margin-bottom: 1rem;
}

.value {
    font-size: 1.5rem;
    font-weight: bold;
    color: #ffffff;
}

.up {
    color: #00ff88;
}

.tradingFeatures {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.featureBox {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 15px;
    padding: 2rem;
    text-align: center;
    transition: transform 0.3s ease;
}

.featureBox:hover {
    transform: translateY(-5px);
}

.featureBox h2 {
    color: #ffd700;
    margin-bottom: 1rem;
    font-size: 1.7rem;
}

.featureBox p {
    color: #cccccc;
    margin-bottom: 1.5rem;
    font-size: 1.6rem;
}

.tradeBtn {
    background: linear-gradient(90deg, #ffd700, #ff8c00);
    border: none;
    padding: 0.8rem 1.5rem;
    border-radius: 25px;
    font-size: 1.7rem;
    color: #1a1a1a;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
}

.tradeBtn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

@media (max-width: 768px) {
    .tradingContainer {
        padding: 1rem;
    }
    
    .header h1 {
        font-size: 2rem;
    }
    
    .statCard {
        padding: 1rem;
    }
}
.loginContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    margin: 2rem auto;
    max-width: 400px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border-radius: 15px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.loginButton {
    margin-top: 1.5rem;
    padding: 1rem 2rem;
    font-size: 1.1rem;
    font-weight: bold;
    color: #ffffff;
    background: linear-gradient(90deg, #00f2fe 0%, #4facfe 100%);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.loginButton:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 20px rgba(79, 172, 254, 0.3);
}

.loginButton:active {
    transform: translateY(0);
}

@media (max-width: 768px) {
    .loginContainer {
        margin: 1rem;
        padding: 1.5rem;
    }

    .loginButton {
        padding: 0.8rem 1.5rem;
        font-size: 1rem;
    }
}

/* Modal styles */
.loginModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure it's on top of everything */
  }
  
  .loginModalContent {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    width: 400px; /* Wider for better spacing */
    position: relative;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* More pronounced shadow */
  }
  
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px; /* Larger close button */
    cursor: pointer;
    color: #777; /* Muted color */
    transition: color 0.3s ease;
  }
  
  .closeButton:hover {
    color: #333; /* Darken on hover */
  }
  
  .loginModalContent h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  .formGroup {
    margin-bottom: 20px; /* More spacing */
  }
  
  .formGroup label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #555;
  }
  
  .formGroup input {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 6px; /* Slightly rounded corners */
    font-size: 16px;
    transition: border-color 0.3s ease;
  }
  
  .formGroup input:focus {
    outline: none;
    border-color: #3498db; /* Highlight on focus */
  }
  
  .loginModalContent button {
    background-color: #3498db;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .loginModalContent button:hover {
    background-color: #2980b9;
  }
  
  /* Optional: Add some animation */
  .loginModalContent {
    animation: fadeIn 0.4s ease-in-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }