.mainContainer {
    padding: 2rem;
}

.levelsContainer {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
    background: linear-gradient(145deg, #1a1a1a, #2d2d2d);
    padding: 2rem;
    border-radius: 15px;
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
    /* width: 60%; */
}
.currentLevelContainer {
    background: linear-gradient(145deg, #2a2a2a, #3d3d3d);
    padding: 2rem;
    border-radius: 12px;
    text-align: center;
    border: 1px solid #ffd700;
    box-shadow: 0 0 30px rgba(255, 215, 0, 0.15);
    height: 350px;
    /* width: 50%; */
    /* float: right; */

    
}

.allLevelsContainer {
    background: linear-gradient(145deg, #2a2a2a, #3d3d3d);
    padding: 2rem;
    border-radius: 12px;
    border: 1px solid #ffd700;
    /* border-bottom: none; */
    /* width: 120%; */
    height: 350px;
   
}

.currentLevelTitle, .levelsTitle {
    color: #ffd700;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
}

.levelsWrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
}

.levelCircle {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 30px;
}

.levelCircle:hover {
    transform: translateY(-5px) scale(1.1);
}

.letter {
    font-size: 2rem;
    font-weight: bold;
    color: #ffffff;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.fullName {
    position: absolute;
    bottom: -45px;
    /* margin-top: 20%; */
    font-size: 1.9rem;
    color: #ffd700;
    white-space: nowrap;
}

.diamond {
    background: linear-gradient(135deg, #B9F2FF 0%, #00C9FF 100%);
    box-shadow: 0 0 20px rgba(0, 201, 255, 0.3);
    border: 2px solid #B9F2FF;
}

.gold {
    background: linear-gradient(135deg, #FFD700 0%, #FFA500 100%);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
    border: 2px solid #FFD700;
}

.silver {
    background: linear-gradient(135deg, #C0C0C0 0%, #808080 100%);
    box-shadow: 0 0 20px rgba(192, 192, 192, 0.3);
    border: 2px solid #C0C0C0;
}

.bronze {
    background: linear-gradient(135deg, #CD7F32 0%, #8B4513 100%);
    box-shadow: 0 0 20px rgba(205, 127, 50, 0.3);
    border: 2px solid #CD7F32;
}

.lead {
    background: linear-gradient(135deg, #4A4A4A 0%, #2B2B2B 100%);
    box-shadow: 0 0 20px rgba(74, 74, 74, 0.3);
    border: 2px solid #4A4A4A;
}

@media (max-width: 768px) {
    .levelsWrapper {
        gap: 1rem;
    }

    .levelCircle {
        width: 60px;
        height: 60px;
    }

    .letter {
        font-size: 1.5rem;
    }

    .fullName {
        font-size: 1.4rem;
    }
}

