.currentLevelCircle {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    position: relative;
    animation: pulseGlow 2s infinite;
}

.currentLevelName {
    position: absolute;
    bottom: -45px;
    font-size: 1.9rem;
    color: #ffd700;
    white-space: nowrap;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

@keyframes pulseGlow {
    0% {
        box-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
    }
    50% {
        box-shadow: 0 0 40px rgba(255, 215, 0, 0.5);
    }
    100% {
        box-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
    }
}

@media (max-width: 768px) {
    .currentLevelContainer {
        padding: 1.5rem;
    }

    .currentLevelCircle {
        width: 100px;
        height: 100px;
    }

    .currentLevelTitle {
        font-size: 1.2rem;
    }

    .currentLevelName {
        font-size: 2rem;
        bottom: -45px;
    }
}

@media (max-width: 768px) {
    .levelsContainer {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .currentLevelContainer, 
    .allLevelsContainer {
        padding: 1rem;
        /* height:99%; */
    }

    .levelsWrapper {
        gap: 1rem;
    }
}